.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 3rem;
  padding: 20px;
  color: white;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.content {
  margin-top: 1rem;
  max-width: 60em;
}

button {
  padding: 1em 2em;
}

button + button {
  margin-left: 0.5em;
}

.params {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

label {
  margin-top: 1em;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.history {
  margin-top: 5em;
  font-size: 80%;
  text-align: left;
}

button {
  background-color: white;
  border: 2px solid #333;
}

.mixer {
  margin-top: 1.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.channel-toggler {
  border: 1px solid #888;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.channel-toggler > span {
  padding: 0.3em;
}

.channel-toggler button + button {
  margin: 0;
}

.mixer button {
  padding: 0.5em;
}

.mixer .active {
  background-color: yellow;
}

label + input {
  margin-left: 0.5em;
}

.seed-input {
  margin-top: 2em;
}

.seed-input input {
  width: 8em;
}

.seed-input button {
  margin-left: 0.5em;
  padding-top: 0.2em;
  padding-bottom: 0.2em;
}



